/* Page Reset */
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

/* Body */

body {
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    background-color: #fff;
    color:black;
    /* font-size: 16px; */
    font-size: 1rem;
    overflow-x: hidden;
}

/* Global Elements */

.body-container {
    width: 100%;
    margin: 0 auto;
    padding: 0;
}

.container {
    width: 100%;
    margin: 0 auto;
    padding: 0;
}

button:hover {
    cursor: pointer;
}

.btn:hover {
    cursor: pointer;
    box-shadow: inset 50px 50px 10px 10px rgba(0, 0, 0, .1);
}

/* Cookies Banner */

.cookies-banner {
    width: 100%;
    background-color: rgba(211, 211, 211, 0.25);
    margin: 0 auto;
    font-size: 13px;
}

.cookies-content {
    display: flex;
    width: 90%;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
    padding: 5px 0;
}

.cookies-content div {
    display: flex;
}

.cookies-content img {
    width: 25px;
    height: 25px;
    filter: opacity(80%);
    margin: 12px;
}

.cookies-content > div > p {
    width: 88%;
}

.cookies-content div:nth-of-type(2) {
    justify-content: space-around;
    flex-basis: 40%;
    flex-wrap: nowrap;
    padding: 0 10px;
}

.cookies-content button {
    margin: 0 5px;
    width: 150px;
    padding: 10px;
    border: none;
    background-color: #EBEBEB;
    font-size: 14px;
}

.cookies-content button:hover {
    cursor: pointer;
    box-shadow: -.5px 4px 7px 2px rgba(0, 0, 0, .2), inset 0 0 0 50px rgba(104, 103, 103, 0.1);
}

/* Dialog and Background Div Overlay */

dialog::backdrop {
    background-color: white;
    opacity: 50%;
}

dialog[open] {
    position: fixed;
    top: 60px;
    display: flex;
    flex-direction: column;
    width: 650px;
    margin: auto;
    padding: 40px 40px;
    border: 1px solid blue;
    font-size: 1rem;
    height: 700px;
}

dialog .close-dialog {
    display: block;
    position: absolute;
    top: 2px;
    right: 2px;
    padding: 10px;
    border: none;
    background: none;
}

dialog .close-dialog:hover {
    cursor: pointer;
}

.manage-cookies div:first-of-type {
    display: flex;
    flex-direction: column;
    overflow-y: scroll;
}

.manage-cookies div:last-of-type {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
}

.manage-cookies div:last-of-type button {
    flex-basis: 49%;
    padding: 8px;
    border: none;
    margin-top: 20px;
    background-color: #EBEBEB;
}

.manage-cookies div:last-of-type button:last-of-type {
    background-color: #0067B8;
    color: white;
}

.manage-cookies div:last-of-type button:last-of-type:disabled {
    background-color: rgba(0, 120, 215, 0.2);
    color: rgba(0, 0, 0, 0.2);
}

.manage-cookies div:last-of-type button:hover {
    box-shadow: -.5px 4px 7px 2px rgba(0, 0, 0, .2), inset 0 0 0 50px rgba(104, 103, 103, 0.1);
}

.manage-cookies div:last-of-type button:disabled {
    opacity: 90%;
    cursor: not-allowed;
    box-shadow: none;
}

dialog fieldset {
    border: none;
}

dialog fieldset input:checked {
    border: 5px solid black;
}

dialog h4 {
    margin-top: 25px;
}

/* Navigation */

nav {
    display: flex;
    position: relative;
    justify-content: space-between;
    align-items: center;
    height: 55px;
    font-size: 13px;
}

.container:nth-of-type(1) {
    width: 95%;
    margin: 0 auto;
    padding: 0;
}

.container:nth-of-type(2) {
    width: 100%;
}

.hamburger_button {
    display: none;
}

/* Search Functionality For Larger Screen */

.search_return_btn {
    height: 70%;
    position: absolute;
    border: 1px solid black;
    top: unset;
    right: 10px;
    width: 75px;
    display: none;
    align-items: center;
    justify-content: center;
    z-index: 5;
}

.search_bar {
    display: none;
    flex-direction: row-reverse;
    border: 1px solid grey;
    position: absolute;
    right: 100px;;
    left: 140px;
    height: 40px;
    z-index: 10;

}

.search_bar input {
    padding-left: 10px;
}

/* Remains Across all Sizes for Search */

.search_return_btn:hover {
    cursor: pointer;
}

/* Activated Class */
.search_return_btn.activated, .search_bar.activated {
    display: flex;
    background-color: white;
}

#search_btn.activated {
    opacity: 0%;
}

.hamburger_button.activated {
    display: none;
}

.search_return_btn img {
    width: 35%;
    
}

.search_bar input {
    width: 100%;
    border: none;
}

.search_bar input::placeholder {
    font-size: inherit;
}

.search_bar input:focus-visible, .search_bar input:focus {
    border: none;
    outline: none;
}

.search_bar #search_submit {
    width: 40px;
    border: none;
    background: none;
}

.search_bar #search_submit img {
    width: 25px;
    display: inline-block;
}

nav img[src*="https"] {
    margin-left: 10px;
    padding-right: 10px;
    width: 120px;
}

nav ul {
    list-style-type: none;
    display: flex;
    flex: 1;
    padding-left: 10px;
    height: 100%;
    align-items: center;
}

nav ul li {
    padding: 0 10px;
}

nav ul li:last-of-type {
    margin-left: auto;
}

nav ul li a {
    text-decoration: none;
    color:rgb(36, 35, 35);
}

nav ul li a:hover {
    border-bottom: 2px solid rgb(36, 35, 35);
    padding-bottom: 3px;
    color:rgb(36, 35, 35);
}

nav #all_microsoft, nav #more_dropdown {
    position: relative;
    display: flex;
    align-items: center;
    height: 100%;
    cursor: pointer;
}

nav #all_microsoft:hover a:first-of-type, nav #more_dropdown:hover a:first-of-type {
    border-bottom: 2px solid rgb(36, 35, 35);
    padding-bottom: 3px;
}

.clicked {
    border: 1px dashed black;
    background-color: rgba(240, 240, 240);
}

nav #all_microsoft .all_micro_content > div {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: flex-start;
}

nav #all_microsoft > a , nav #more_dropdown > a{
    padding-bottom: 3px;
    padding-right: 5px;
    height: 20px;
}

nav #all_microsoft > img, #more_dropdown > img {
    width: 10px;
    transform: rotate(270deg);
}

.all_micro_content {
    display: none;
    position: absolute;
    background-color:rgba(240, 240, 240);
    border: 1px solid rgba(225, 225, 225, 0.8);
    border-bottom: 2px solid black;
    width: 94vw;
    margin: 0 auto;
    box-sizing: border-box;
    top: 50px;
    right: -150px;
    transition: opacity .2s;
    z-index: 20;

}

.all_micro_content .footer-column {
    display: block;
    padding-top: 20px;
    min-width: 200px;
    flex-basis: 200px;
}

.all_micro_content .footer-column > li {
    padding-bottom: 40px;
}

.all_micro_content .footer-column a li {
    padding-bottom: 30px
}

.all_micro_content .footer-column a:hover {
    text-decoration: underline;
}

.all_micro_content .go-to-sitemap {
    width: 100%;
    padding: 10px 0px;
    border: none;
    background-color:rgb(225, 225, 225);
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: .75rem;
}

.go-to-sitemap img {
    width: 15px;
    transform: rotate(180deg);
    font-size: .5rem;
}

/* Nav Right Icons */

nav div .nav-icons-right {
    display: inline-block;
}

.nav-icons-right img {
    display: inline-block;
    width: 25px;
    margin: 0 10px;
}

/* Nav More Dropdown */

nav #more_dropdown {
    position: relative;
    display: none;
}

nav .more_div {
    position: absolute;
    top: 50px;
    left: -1px;
    flex-direction: column;
    background-color:rgba(240, 240, 240);
    border: 1px solid rgba(225, 225, 225, 0.8);
    border-bottom: 2px solid black;
    width: 300%;
    padding: 0 20px;
    z-index: 15;
}

nav .more_div div {
    padding: 20px 0;
}

.more_div.more_1 {
    display: none;
}

.more_div.more_2 {
    display: none;
}

/* Showcase / Slider */

.slider {
    width: 200%;
    display: flex;
    /* overflow: hidden; */
    flex-direction: row;
}

.slider.reverse {
    flex-direction: row-reverse;
}

.slide, .showcase-two, .showcase-three {
    width: 100%;
    position: relative;
    top: 0;
}

.slide img, .showcase-two img, .showcase-three img {
    width: 100%;
}

.slide > div {
    width: 30%;
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 32%;
    left: 115px;
}

.showcase-two > div, .showcase-three > div {
    width: 37.5%;
    display: flex;
    flex-direction: column;
    background-color: white;
    padding: 47px;
    position: absolute;
    margin-top: -400px;
    left: 5%;
    box-shadow: 0 0.1875rem 0.4375rem 0 rgb(0 0 0 / 13%), 0 0.0625rem 0.125rem 0 rgb(0 0 0 / 11%);
}

.showcase-three > div {
    margin-top: -430px;
}

.slide:nth-of-type(2) > div{
    width: 30%;
}

.slide:nth-of-type(2) .btn_group > a {
    padding-top: .5rem;
    padding-bottom: .5rem;
    /* color: black; */
    opacity: 100%;
}

.slide:nth-of-type(2) .btn_group > a:hover {
    text-decoration: underline;
}

.slide:nth-of-type(2) .btn_group > a:hover img {
    margin-left: 5px;
}

.slide:nth-of-type(2) .imgcardarrow {
    width: 24px;
    filter: invert(30%) sepia(89%) saturate(6133%) hue-rotate(235deg) brightness(99%) contrast(93%);
    opacity: 100%;
}

.slide h2, .showcase-two h2, .showcase-three h2 {
    margin-bottom: 10px;
    font-weight: 500;
    font-size: 1.5rem;
}

.slide div p, .showcase-two div p, .showcase-three div p {
    line-height: 1.5rem;
    /* margin-bottom: 10px; */
    margin: .6rem 0;
}

.slide .btn_group {
    display: flex;
    flex-wrap: wrap;
    width: 90%;
}

.slide .btn, .showcase-two .btn, .showcase-three .btn {
    width: fit-content;
    padding: .625rem .75rem;
    border-radius: .125rem;
    font-weight: 600;
    border: .125rem solid transparent;
    background-color: #0067b8;
    margin-right: 1.5rem;
    margin-top: .5rem;
    margin-bottom: .5rem;
    line-height: 1rem;
    color: white;
}

.slide .btn a {
    display: flex;
    justify-content: center;
    align-items: center;
}

.slide .btn img {
    width: 20px;
    filter: invert();
}

.slide .imgcardarrow {
    width: 24px;
    filter: invert(30%) sepia(89%) saturate(6133%) hue-rotate(235deg) brightness(99%) contrast(93%);
}

.slide .btn_group > a {
    display: flex;
    align-items: center;
    text-decoration: none;
    font-weight: 500;
    opacity: .75;
    padding: 10px 0;
    color: #0067b8;
}

.slider-nav {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 6px;
    width: 100%;
    background-color: white;
}

.slider__btn {
    background: none;
    border: none;
    width: 48px;
    height: 48px;
}

.slider__btn img {
    padding: 15px 15px;
}

.slider__btn.slider__btn--left:hover img, .slider__btn.slider__btn--right:hover img {
    padding: 15px 25px 15px 5px;
}

.slider__btn.slider__btn--play img {
    width: 100%;
}

.slider__btn.slider__btn--play img[src="/images/pause-button.png"] {
    filter: invert();
} 

.slider__btn.slider__btn--play:hover img {
    filter: invert();
}

.slider-nav button:last-of-type img {
    transform: rotate(180deg);
}

/* Slider Dots */
.dots {
    display: flex;
    margin: 0px 15px;
}
  
.dots__dot {
    border: 1px solid black;
    background-color: white;
    height: .6rem;
    width: .6rem;
    border-radius: 50%;
    margin-right: 2rem;
    transition: all 0.2s;
}

.dots__dot:last-child {
    margin: 0;
}

.dots__dot--active {
background-color: black;
}

/*Link Cards*/

.linkcards {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: 2rem 0 3.5rem 0;
}

.linkcards .card {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    text-decoration: none;
    color: black;
    font-size: .85em;
    margin: 1.5rem;
}

.linkcards .card img {
    width: 40px;
    padding-bottom: 5px;
}

.card a {
    color: #0067b8;
    font-weight: 600;
}

.card a h3{
    font-weight: inherit;
}

.card a:hover {
    cursor: pointer;
}

/* Image Cards 1 */

.image-cards {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    width: 100%;
    grid-gap: 25px;
    margin-bottom: 30px;
}

.image-cards .imgcard h2{
    padding-top: 30px;
    padding-bottom: 5px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 600;
    color: black;
    font-size: 21px;
}

.image-cards .imgcard img {
    width: 100%;
}

.image-cards .imgcard img.imgcardarrow {
    width: 24px;
    filter: invert(30%) sepia(89%) saturate(6133%) hue-rotate(235deg) brightness(99%) contrast(93%);
}

.imgcard a {
    display: flex;
    align-items: center;
    text-decoration: none;
    font-weight: 500;
    opacity: .75;
    padding: 10px 0;
    /* position: relative; */
}

.imgcard div {
    cursor: pointer;
}

.imgcard a:hover {
    text-decoration: underline;
    cursor: pointer;
}

.imgcard a:hover img {
    margin-left: 5px;
}

.imgcard:nth-of-type(1) p {
    width: 90%;
}

/* Showcase 2 & Showcase 3 */

.showcase-two .btn a, .showcase-three .btn a {
    display: flex;
    justify-content: center;
    align-items: center;
}

.showcase-two .btn img, .showcase-three .btn img {
    width: 20px;
    filter: invert();
}

/* Image Cards 2 */

.container h2#businessheader {
    font-weight: 500;
    padding-bottom: 20px;
    font-size: 24px;
}

.image-cards .imgcard .newicon {
    display: inline-block;
    background-color: gold;
    margin-top: 10px;
    padding: 3px 15px;
    font-size: .9em;
    letter-spacing: .06em;
    margin-bottom: 5px;
    font-weight: 600;
}

.image-cards .imgcard .newicon+h2 {
    padding-top: 0px !important;
}

.imgcard:nth-of-type(2) h2, .imgcard:nth-of-type(2) p {
    width: 95%;
}

.imgcard:nth-of-type(2) h2, .imgcard:nth-of-type(4) h2 {
    line-height: 1.2em;
}

/* Social Area */

.container .socialarea {
    display: flex;
    align-items: center;
    margin-bottom: 100px;
    margin-top: 50px;
}

.container .socialarea h4 {
    font-weight: normal;
    padding-right: 5px;
}

.socialarea .social-icon {
    width: 28px;
    margin: 0 9px;
}

/* Back to Top Button */
    .container:nth-of-type(10) {
        position: sticky;
        bottom: -50px;
    }

    .back-to-top {
        display: flex;
        position: absolute;
        bottom: 0;
        right: 0;
        justify-content: center;
        align-items: center;
        width: 135px;
        padding: .625rem .75rem;
        border-radius: .125rem;
        background-color: lightgrey;
        border: none;
        font-weight: 600;
        font-size: 1rem;
        margin-bottom: 60px;
        text-decoration: none;
        color: black;
    }

    .back-to-top.clear {
        transition: opacity .5s;
        opacity: 100%;
        z-index: unset;
    }

    .back-to-top.faded {
        transition: opacity .5s;
        opacity: 0%;
    }

    .back-to-top:hover {
        cursor: pointer;
        box-shadow: 0 0.125rem 0.25rem 0 rgb(0 0 0 / 13%), 0 0 0.0625rem 0 rgb(0 0 0 / 11%);
        background-color: white;
    }

    .back-to-top > img {
        width: 14px;
        transform: rotate(90deg);
        margin-right: .5rem;
    }

/* Sitemap Footer */

footer {
    background-color: rgba(230, 228, 228, 0.712);
    color: rgb(95, 95, 95);
    width: 100%;
}

footer .container:nth-of-type(1) {
    width: unset;
    padding: 0 12px;
}

footer a, footer li {
    font-size: 11px;
    padding: 8px 0;
    color: inherit;
    text-decoration: none;
    line-height: 16px;
}

footer .language-copyright a {
    font-size: 11px;
    padding: 30px 0 16px 0;
    color: inherit;
    text-decoration: none;
    line-height: 16px;
}

footer .language-copyright li {
    font-size: 11px;
    padding: 0px;
    color: inherit;
    text-decoration: none;
    line-height: 16px;
}

footer .language-copyright ul > li {
    padding: 30px 0 16px 0;
}

footer a:hover {
    text-decoration: underline;
    color: black;
}

footer .sitemap {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    /* padding: 30px 0; */
}

.sitemap ul {
    list-style-type: none;
    padding: 28px 12px 0px 12px;
}

.sitemap .footer-column a:first-child li {
    font-weight: 600;
    font-size: 15px;
}

.sitemap .footer-column a:first-child:hover {
    text-decoration: none;
    cursor: default;
}

.sitemap ul:nth-of-type(3) a:nth-of-type(7) li {
    width: 75%;
}

/* Language and Copyright*/

/* footer .language-copyright {
    padding-bottom: 10px;
} */

footer .language-copyright ul {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    list-style-type: none;
    width: 100%;
}

footer .language-copyright > a:first-of-type li::before {
    content: url(/images/globe.svg) '';
    display: inline-block;
    vertical-align: middle;
    padding-right: 8px;
}
footer .language-copyright {
    display: flex;
    /* justify-content: space-between; */
    flex-direction: row;
    align-items: center;
    width: 100%;
}

footer .language-copyright a:first-child li {
    /* padding-right: 5px; */
    list-style-type: none;
}

footer .language-copyright > a:first-child {
    flex-basis: 225px;
}

/*Media Queries*/

@media (max-width: 1275px) {
    footer .language-copyright {
        display: block;
        padding: 0 12px;
    }

    footer .language-copyright > a:first-child {
        display: block;
        flex-basis: 225px;
        align-self: flex-start;
        /* height: 1rem; */
    }

    footer .language-copyright ul {
        padding-bottom: 20px;
    }

    footer .language-copyright ul > li {
        padding: 0px 16px 0px 0;
        margin-top: 8px;
    }

    footer .language-copyright a {
        font-size: 11px;
        padding: 0px 16px 0px 0;
        margin-top: 8px;
        color: inherit;
        text-decoration: none;
        line-height: 16px;
    }

    footer .language-copyright > a {
        font-size: 11px;
        padding: 0px 0 0px 0;
        margin-top: 32px;
        color: inherit;
        text-decoration: none;
        line-height: 16px;
    }
}

@media (min-width: 1084px) and (max-width: 1179px) {
    .slide > div {
        top: 20%;
        left: 100px;
    }
}

@media (min-width: 1084px) {

    .container {
        width: 90%;
        margin: 0 auto;
        padding: 0;
    }

    .container:nth-of-type(1) {
        width: 90%;
        margin: 0 auto;
        padding: 0;
    }

    .all_micro_content {
        right: -175px;
    }

    .slide h2, .showcase-two h2, .showcase-three h2 {
        margin-bottom: 10px;
        font-weight: 500;
        font-size: 1.8rem;
    }

    .slide:nth-of-type(2) .imgcardarrow {
        filter: unset;
        opacity: 100%;
    }

    .slide .btn_group > a {
    color: black;
    }

    .showcase-two {
        margin: 5.5rem 0;
    }

    .container h2#businessheader {
        font-size: 1.8rem;
    }

    .linkcards .card {
        margin: 1rem 1.5rem;
    }

    .image-cards {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        width: 100%;
        grid-gap: 25px;
        margin-bottom: 30px;
    }

    .image-cards .imgcard h2 {
        padding-top: 2.5rem;
        padding-bottom: .75rem;
        font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
        font-weight: 600;
        color: black;
        font-size: 1.5rem;
    }

    .image-cards .imgcard p {
        line-height: 1.5rem;
    }

    .back-to-top {
        right: -4.5%;
    }

    footer .sitemap {
        grid-template-columns: repeat(6, 1fr);
    }

    .sitemap ul {
        list-style-type: none;
        padding: 28px 0px 0px 0px;
    }

    footer .container:nth-of-type(1) {
        width: unset;
        padding: 0 5%;
    }
}

@media (min-width: 900px) and (max-width: 1083px) {

    .slide {
        margin-bottom: -25px;
    }

    .slide > div, .showcase-two > div, .showcase-three > div {
        width: 97.5%;
        display: flex;
        flex-direction: column;
        position: relative;
        box-shadow: 0 0.1875rem 0.4375rem 0 rgb(0 0 0 / 13%), 0 0.0625rem 0.125rem 0 rgb(0 0 0 / 11%);
        background-color: white;
        margin: 0 auto;
        top: -50px;
